.ecl-label{border-radius:0;color:#404040;display:inline-block;font:normal normal 400 .875rem/1rem arial,sans-serif;margin:0;text-transform:uppercase}.ecl-label--low{background-color:#f5f5f5;border:1px solid #cfcfcf;padding:calc(.5rem - 1px) calc(.75rem - 1px)}.ecl-label--medium{background-color:#fff4bb}.ecl-label--high,.ecl-label--medium{border:0 solid transparent;padding:.5rem .75rem}.ecl-label--high{background-color:#ffd617}.ecl-label--highlight{background-color:#004494;border:0 solid transparent;color:#fff;padding:.5rem .75rem}